import { useStaticQuery, graphql } from 'gatsby'
import dedupe from 'dedupe'

export const useBlogCategoriesKanash = () => {
  const { kanashCatagory } = useStaticQuery(categoriesQueryKan)
  return kanashCatagory.nodes
    ? dedupe(kanashCatagory.nodes, node => node.slug)
    : null
}

const categoriesQueryKan = graphql`
  query allArticleCategoryQueryKan {
    kanashCatagory: allArticleCategory(
      filter: {
        id: {
          in: [
            "5a2fdb42-b194-53d7-80ca-8e4e55b67d3d"
            "753f6c71-e42d-5e51-b3a0-fe00c5d91bda"
            "ff98f8c5-e946-5b71-a9c6-f71beefe6573"
          ]
        }
      }
      limit: 2000
    ) {
      nodes {
        ...ArticleCategory
      }
    }
  }
`
